@use "global" as *;

.p-card {
  display: inline-block;
}

.p-card__img {
  position: relative;
}

.p-card__img::before {
  content: "";
  display: block;
  padding-top: calc(210/335 * 100%);
}

.p-card__img img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.p-card__body {
  margin-top: rem(14);
}

.p-card__title {
  text-align: center;
  font-size: rem(18);
  font-weight: $bold;
  font-family: $english-family;
  letter-spacing: 0.05em;
}

.p-card__text {
  font-size: rem(13);
  font-weight: 300;
  line-height: calc(25/14);
  letter-spacing: 0.05em;
  margin-top: rem(10);
}