@use "global" as *;

.p-header {
  height: rem(62);
  background-color: transparent;
  // background-color: #21054D;
  z-index: 1000;
  position: fixed;
  width: 100%;
}

.p-header__inner {
  display: flex;
  justify-content: space-between;
  height: inherit;
  align-items: center;
  padding: 0 rem(15);
  @include mq(md) {
    padding: 0 rem(30);
   
  }
}

.p-header__drawer {
  margin-left: auto;
  @include mq(md) {
   display: none;
  }
}

.p-header__logo {
  height: inherit;
}

.p-header__logo-link {
  color: $white;
  font-size: rem(18);
  display: flex;
  align-items: center;
  height: inherit;
}

.p-header__pc-nav {
  height: inherit;
  display: none;
  @include mq(md) {
   display: block;
   margin-left: auto;
  }
}